export default {
  namespaced: true, // 네임스페이스 붙이겠다고 선언하는 거임.

  state: {
    count: 0,
  },
  getters: {
    getCount(state, getters, rootState, rootGetters) {
      return state.count;
    },
  },
  mutations: {
    setCount(state, payload) {
      state.count += payload;
    },
  },
  actions: {
    // payload: {value:1, duration:3000}이라고 치자.
    setCountByAsync(context, payload) {
      new Promise((resolve, reject) => {
        setTimeout(() => {
          resolve(payload.value);
        }, payload.duration);
      })
        .then((data) => {
          context.commit("setCount", data);
          console.log("count 상태변경 성공");
        })
        .catch((error) => {
          console.log("count 상태변경 실패");
          console.log(error);
        });
    },
  },
  modules: {},
};
