import { createApp } from "vue";
import App from "./App.vue";
import router from "./router"; //  -> ./router/index.js를 생략한것과 마찬가지임. 폴더 밑에 index.js있으면 전부 해당되는 방식
import store from "./store";
// -> 모듈 import를 여러 번 한다 하더라도 모듈 실행은 딱 한 번 된다. 그리고 공유된다고...

// axios.defaults.baseURL 을 먼저 적용하기 위해 import.
// 브라우저 리프레시할 때 apis를 이용하는 페이지가 있을 수 있기 때문.
import axiosConfig from "./apis/axiosConfig";

createApp(App).use(store).use(router).mount("#app"); // #app자리에 App 컴포넌트를 넣어라 라는 소리임.
// <router-view/> 태그가 나타나는 곳마다,
// router가 url를 보고 적절한 컴포넌트를 개발자가 router 폴더에서 정의한대로 알아서 넣어준다.
