import { createStore } from "vuex";
import counter from "./counter";
import axiosConfig from "@/apis/axiosConfig";

export default createStore({
  state: {
    userId: "", // mid를 저장할 state.
    authToken: "", // AccessToken을 저장할 state.
  },
  getters: {
    getUserId(state, getters, rootState, rootGetters) {
      return state.userId;
    },
    // getUserId: function(state, getters, rootState, rootGetters) {...} 이렇게 작성해도 됨

    getAuthToken(state, getters, rootState, rootGetters) {
      return state.authToken;
    },
  },
  mutations: {
    setUserId(state, payload) {
      state.userId = payload;
    },
    setAuthToken(state, payload) {
      state.authToken = payload;
    },
  },
  actions: {
    //payload: {userId:"xxxx", duration:3000}
    setUserIdByAsync(context, payload) {
      console.log("context:", context);
      console.log("payload:", payload);
      // context는 고정적으로 들어오는 파라미터.
      new Promise((resolve, reject) => {
        //시간을 요하는 작업(주로 Back-end로 데이터 요청)
        setTimeout(() => {
          //성공적으로 응답을 얻었을 경우
          resolve(payload.userId);
        }, payload.duration);
      })
        .then((data) => {
          //resolve가 되었을 경우 mutations을 이용해서 상태값 변경
          context.commit("setUserId", data); // setter를 바로 호출하는게 아니라, commit을 통해 호출하는 방식임.
          console.log("userId 상태 변경 성공");
        })
        .catch((error) => {
          //예외가 발생했거나, reject된경우
          console.log("userId 상태 변경 실패");
        });
    },

    loadAuth(context, payload) {
      context.commit("setUserId", sessionStorage.getItem("userId") || "");
      context.commit("setAuthToken", sessionStorage.getItem("authToken") || "");
      // 로드된 authToken이 정상적으로 존재한다면, 헤더에 토큰을 담아라.
      if (context.state.authToken !== "") axiosConfig.addAuthHeader(context.state.authToken);
    },

    // 로그인이 성공했을 때 실행되어서 상태의 userId와 authToken을 저장하는 것.
    // payload: {userId: xxxx, authToken: yyyy} 이런 식임
    saveAuth(context, payload) {
      context.commit("setUserId", payload.userId);
      context.commit("setAuthToken", payload.authToken);

      // *** 가장 중요 ***
      axiosConfig.addAuthHeader(payload.authToken); // rest API 요청할때마다 Authorization 헤더에 토큰을 담아 보내야 하는데, 매번 보내는게 아니고 로그인 성공 이후에는 모든 요청에 Authorization header가 추가가 될 수 있도록 설정해주는 것임. (이 코드 한 줄로 이제 앞으로 모든 토큰 요청시에 인증헤더에 담아 보낼 수 있게 되는 것임)

      //2022.05.31.(화) 실습내용
      sessionStorage.setItem("userId", payload.userId);
      sessionStorage.setItem("authToken", payload.authToken);
    },

    // 로그아웃했을 때 실행되어서 상태의 userId와 authToken을 삭제하는 것.
    deleteAuth(context, payload) {
      context.commit("setUserId", "");
      context.commit("setAuthToken", "");

      // *** 가장 중요 ***
      axiosConfig.removeAuthHeader();

      //2022.05.31.(화) 실습내용
      sessionStorage.removeItem("userId");
      sessionStorage.removeItem("authToken");
    },
  },
  modules: {
    counter,
  },
});
