import axios from "axios";
// import { useStore } from "vuex"; //WRONG!!!!!!!!!! 이건 컴포넌트 안에서나 쓸 수 있는 hook임.
import store from "@/store"; //이렇게 해야 함.

async function join(user) {
  try {
    const response = await axios.post("/member/join", {
      mid: user.id,
      mname: user.name,
      mpassword: user.password,
      mrole: user.role,
      memail: user.email,
    }); // 백엔드 api에 따르면, body에 json객체를 담아 서버에 보내줘야 함.
    return response.data.result;
  } catch (error) {
    console.log(error);
    return "fail-network";
  }
}

async function login(user) {
  try {
    const response = await axios.post("/member/login", {
      mid: user.id,
      mpassword: user.password,
    });
    // 200 응답으로 잘 받아오면 여기서 store를 쓰면 됨.
    store.dispatch("saveAuth", { userId: response.data.mid, authToken: response.data.accessToken });
    return "success";
  } catch (error) {
    // 401 같은 에러가 나면 여기 catch에 걸림
    if (error.response) {
      if (error.response.status === 401) {
        return "fail-401";
      }
    } else {
      return "fail-network"; // response조차 없을 때가 있음.
    }
  }
}

async function logout() {
  try {
    await axios.get("/member/logout"); // 이건 서버쪽만 정리해주는 요청
    //프론트쪽도 정리를 해줘야 함. userId날리고, authToken날리고,
    //요청헤더의 Authorization Header도 정리해야 함.
    //스토어에 만들어둔 deleteAuth라는 액션메소드를 정의한 바 있음. 이걸 활용하면 됨~
    store.dispatch("deleteAuth");
  } catch (error) {
    console.log(error);
  }
}

async function refreshToken() {
  //어..? 갱신을 요청하는데 authToken이 없다? 말이 안됨; 그래서 store.state.authToken !== "" <<이 경우만 따지는 것임.
  try {
    console.log("hey");
    console.log(store.state.authToken);
    if (store.state.authToken !== "") {
      console.log(store.state.authToken);
      const response = await axios.get("/member/refreshToken");
      console.log("response");
      console.log(response);
      store.dispatch("saveAuth", {
        userId: response.data.mid,
        authToken: response.data.newAccessToken,
      });
      return true; // 성공적으로 갱신되면 true return,
    }
  } catch (error) {
    console.log(error);
  }
  return false; // 갱신 실패하면 false return,
}

export default {
  join,
  login,
  logout,
  refreshToken,
};
