import { createRouter, createWebHistory } from "vue-router";
import HomeView from "../views/HomeView.vue";
import menu01 from "./menu01";
import menu02 from "./menu02";
import menu03 from "./menu03";
import menu04 from "./menu04";
import menu05 from "./menu05";
import menu06 from "./menu06";
import menu07 from "./menu07";

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView, // 비-lazy-load 방식
  },
  {
    path: "/about",
    name: "about", // 경로 별명지어주는거임(요즘엔 잘 안쓴다고...)
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    // 아래 화살표함수는 주로 콜백용임. 사용자가 path에 지정된 경로로 화면을 바꾸었을때, component에 지정된 콜백함수를 그제서야 실행시키겠다~ 라는 의미임.
    component: () => import(/* webpackChunkName: "about" */ "../views/AboutView.vue"),

    // 위 코드 대신 그 아래처럼 작성해도 되나, 위의 lazy-load 방식이 더 효율적이겠지.
    // component: AboutView,

    // home화면은 콜백보다는 그냥 바로 다운받는게 더 좋다. 첫 랜더링화면이니까.
  },
  ...menu01,
  ...menu02,
  ...menu03,
  ...menu04,
  ...menu05,
  ...menu06,
  ...menu07,
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL), // 브라우저에는 url이 바뀌면 자동적으로 history 객체에 정보가 담긴다. 이 history 정보를 이용을 하겠다는 설정임. (지금은 디폴트인 '/'로 설정한 상태)
  routes,
});

export default router;
